import React from "react"
import PasteDelegate from './PasteDelegate';

class Comp extends React.Component {

  render() {
    return <PasteDelegate>{(state) => {
      const text = state.text || `蚂蚁财富:1000\n华泰:2000`;
      const total = text.replace(/.+?:(.+)/gm, '$1').split('\n').map(v => v.trim()).reduce((p, v) => +p + (+v), 0).toFixed(2);
      return <React.Fragment>
        <pre style={{whiteSpace: 'pre'}} dangerouslySetInnerHTML={{__html: text}}/>
        <section>
          Total:{total}
        </section>
      </React.Fragment>
    }}</PasteDelegate>
  }
}


export default Comp;
